:root {
  --primary: #13B497;
  --secondary: #D07407;
  --secondary-light: #f78f13;
  --primary-hover: #0e8670;
  --primary-dark: #042a23;
  --rgba-primary-1: rgba(19, 180, 151, 0.1);
  --rgba-primary-2: rgba(19, 180, 151, 0.2);
  --rgba-primary-3: rgba(19, 180, 151, 0.3);
